.button {
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  cursor: pointer;
  margin: 5px;
  padding: 5px;
  background-color: #204b77;
  text-decoration: none;
  color: #e0f2ff;
  height: 32px;
  transition: background-color 0.2s;

  &.wide {
    background-color: #527aa2;
    min-width: 110px;
  }

  &:hover {
    background-color: #3b556f;
  }

  &.light {
    background-color: #527aa2;

    &:hover {
      background-color: #7296bb;
    }
  }

  > img {
    max-width: 20px;
  }
}
