.app {
  position: absolute;
  height: 100%;
  width: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;

  header {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #204b77;
    color: white;
    box-shadow: 0px -8px 10px 10px rgba(0, 0, 0, 0.5);
    z-index: 10;

    a,
    button {
      font-size: inherit;
      font-family: inherit;
      border: none;
      display: inline-block;
      margin: 5px;
      padding: 5px;
      background-color: #527aa2;
      text-decoration: none;
      color: #b3dfff;
      transition: background-color 0.2s;

      &:hover {
        background-color: #7296bb;
      }
    }

    .saveButton {
      margin-top: 5px;
      fill: #00ff47;

      &.saving {
        fill: orange;
        animation: blinker 300ms linear infinite;
      }
    }

    @keyframes blinker {
      0% {
        opacity: 1;
      }
      40% {
        opacity: 1;
      }
      50% {
        opacity: 0;
      }
      90% {
        opacity: 0;
      }
    }
  }

  section {
    position: relative;
    background-color: #dbe7f1;
    flex-grow: 1;
    overflow-x: auto;
  }
}
