.field {
  &.isMaximized {
    background-color: beige;
    margin: 0;
    padding: 10px;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: column;

    .children {
      flex-grow: 1;

      > * {
        height: 100%;
      }
    }
  }

  .label {
    text-align: left;

    .button {
      float: right;

      &:hover {
        cursor: pointer;
        color: gray;
      }
    }
  }

  input {
    width: 100%;
    font-size: 12pt;
  }

  textarea {
    width: 100%;
    font-size: 12pt;
    height: 200px;
  }
}
