.taskList {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;

  > .tasksFilter {
    margin-top: -37px;
    background-color: #de8d27;
    box-shadow: none;
    transition: margin-top 0.3s;

    &.visible {
      margin-top: 0;
      box-shadow: 0px -8px 10px 10px rgba(0, 0, 0, 0.5);
    }

    > .tasksFilterInner {
      display: flex;
      justify-content: center;
      align-items: center;
      max-width: 622px;
      margin-left: auto;
      margin-right: auto;

      > .taskFilterText {
        background-color: white;
        border: 1px solid black;
        margin: 5px;
        padding: 5px;
        border-radius: 9px;
        flex-grow: 1;
      }
    }
  }

  > .tasks {
    flex-grow: 1;
    overflow-y: auto;
    scrollbar-width: thin;
  }

  > .tasksButtons {
    background-color: #77a4d2;
  }
}

.task {
  cursor: pointer;
  background-color: white;
  margin: 10px auto;
  padding: 5px;
  box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 0.5);
  display: flex;
  max-width: 600px;

  > .title {
    flex-grow: 1;
  }
}

.taskForm {
  background-color: blue;
  padding: 20px;

  .button:hover {
    background-color: #527aa2;
  }
}

.taskStateIndicator {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 30px;
  margin-left: auto;
  margin-right: auto;
  padding: 2px;
  border: 1px solid black;
  border-radius: 5px;
  transition: opacity 0.2s;

  &.notCurrent {
    opacity: 0.5;
  }

  &.clickable {
    cursor: pointer;

    &:hover {
      opacity: 1;
      border-width: 3px;
    }
  }

  &.thickBorder {
    border-width: 3px;
  }

  &.small {
    height: 21px;
    width: 21px;
  }
}

.taskStateClosed {
  display: flex;
  justify-content: center;

  > * {
    margin: 0 5px;
  }
}
