.loadStatus {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  text-align: center;
  font-size: 1.5rem;
  color: #436a8c;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #dbe7f1;

  > img {
    width: 100px;
    height: 100px;
    margin-bottom: 20px;
  }

  .loadBar {
    width: 80%;
    height: 30px;
    margin-bottom: 20px;
  }
}
