div.tag {
  background-color: #ffe182;
  padding: 2px 7px;
  display: inline-block;
  border-radius: 9999px;

  &.noTag {
    background-color: #e0e0e0;
  }

  &.clickable {
    cursor: pointer;
    &:hover {
      background-color: #f7c114;

      &.noTag {
        background-color: #bfbfbf;
      }
    }
  }

  &.active {
    background-color: #f7c114;

    &.noTag {
      background-color: #bfbfbf;
    }
  }

  span.remove {
    background-color: #ca9a00;
    border-radius: 100%;
    height: 18px;
    width: 18px;
    display: inline-block;
    margin-left: 5px;
    font-size: 14px;
  }
}
